import React from 'react';
import PropTypes from 'prop-types';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import { packagesAndModules as style } from '@webui-one/styles/ConfigurationEditorStyles';

const Widget = ({ title, children }) => (
    <div style={{ backgroundColor: defaultStyles.color3, height: '100%' }}>
        <div style={style.headerContainer}>
            <p style={style.header}>{title}</p>
        </div>
        <div style={style.contentContainer}>
            {children}
        </div>
    </div>
);

Widget.propTypes = {
    title: PropTypes.string,
    children: PropTypes.element,
};

export default Widget;
