import {
    useCallback,
    useEffect,
    useReducer,
    useState,
} from 'react';

const prefix = 'hooks/';

export const LOADING = `${prefix}LOADING`;
export const PROGRESS = `${prefix}PROGRESS`;
export const COMPLETE = `${prefix}COMPLETE`;
export const ERROR = `${prefix}ERROR`;

export const isLoading = state => state.status === LOADING;
export const isComplete = state => state.status === COMPLETE;
export const isError = state => state.status === ERROR;

const initialState = {
    status: undefined,
    response: undefined,
    error: undefined,
    progress: 0,
};

const reducer = (state = initialState, { type, response, progress, error } = {}) => {
    switch (type) {
    case LOADING:
        return {
            ...state,
            status: LOADING,
            response: undefined,
            error: undefined,
            progress: 0,
        };
    case COMPLETE:
        return {
            ...state,
            status: COMPLETE,
            response,
            error: undefined,
            progress: 0,
        };
    case PROGRESS:
        return {
            ...state,
            progress,
        };
    case ERROR:
        return {
            ...state,
            status: ERROR,
            response: undefined,
            error,
            progress: 0,
        };
    default:
        return state;
    }
};

const loading = () => ({ type: LOADING });
const complete = (response, paths) => ({
    type: COMPLETE, response, paths,
});
const setProgress = progress => ({
    type: PROGRESS, progress,
});
const errorAction = e => ({ type: ERROR, error: e });

const uploadPackageMock = async () => 'done';

export const usePackageUpload = ({
    th,
    funcs: {
        uploadPackageFunc = uploadPackageMock,
    } = {},
} = {}) => {
    const [state, dispatch] = useReducer(reducer, initialState);
    const [execObject, setExecuting] = useState({
        executing: false,
        data: undefined,
        onDone: undefined,
    });

    const exec = useCallback(({ data, onDone }) => setExecuting({
        executing: true,
        data,
        onDone,
    }), []);

    useEffect(() => {
        if (!execObject.executing) return () => null;
        if (!th) return () => null;
        let isCanceled = false;
        const controller = new AbortController();
        const signal = controller.signal;


        async function runAsync() {
            dispatch(loading());
            try {
                const response = await uploadPackageFunc({
                    signal,
                    th,
                    data: execObject.data,
                });

                if (!isCanceled) {
                    dispatch(setProgress(50));
                    setTimeout(() => {
                        dispatch(complete(response));
                        execObject.onDone();
                    }, 1000);
                }
            } catch (err) {
                if (!isCanceled) {
                    dispatch(errorAction(err));
                }
            }
            if (!isCanceled) {
                setExecuting({
                    executing: false,
                    data: undefined,
                    onDone: undefined,
                });
            }
        }
        runAsync();

        return () => {
            isCanceled = true;
            controller.abort();
        };
    }, [execObject, uploadPackageFunc, th]);

    return [state, exec];
};
