import React from 'react';
import PropTypes from 'prop-types';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import progressBarStyles from './ProgressBarStyles.css';

const styles = {
    text: {
        ...defaultStyles.fontBold,
        color: defaultStyles.color1,
        fontSize: defaultStyles.large1,
    },
};

const ProgressBar = ({ progress }) => {
    const formattedProgress = `${progress}%`;

    return (
        <div>
            <progress className={progressBarStyles.progressBar} max="100" value={progress} />

            <p style={styles.text}>
                Uploaded
                {' '}
                {formattedProgress}
            </p>
        </div>
    );
};

ProgressBar.propTypes = {
    progress: PropTypes.number,
};

export default ProgressBar;
