import React, { useRef, useState } from 'react';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import ProgressBar from '../components/ProgressBar';
import { usePackageUpload, isLoading } from './usePackageUploadHook';


const style = {
    form: {
        display: 'flex',
        width: '100%',
    },
    innerLeftContainer: {
        flex: 2,
    },
    fileLabel: {
        padding: defaultStyles.small1,
        display: 'inline-block',
        marginBottom: defaultStyles.small1,
        maxWidth: '100%',
        minWidth: '220px',
        color: defaultStyles.color8,
        border: `1px solid ${defaultStyles.color7}`,
        boxShadow: `inset 0px 2px 2px ${defaultStyles.color7}`,
        borderRadius: '2px',
        fontSize: defaultStyles.medium2,
    },
    fileInput: {
        display: 'none',
    },
    fileInfo: {
        fontSize: defaultStyles.medium1,
    },
};

const getSizeEnding = (sizeEndingAsNumber) => {
    switch (sizeEndingAsNumber) {
    case 0:
        return 'b';
    case 1:
        return 'kb';
    case 2:
        return 'mb';
    case 3:
        return 'gb';
    default:
        return 'b';
    }
};

const getFileSizeShortening = (fileSize) => {
    const toDivideBy = 1024;
    let size = fileSize;
    let times = 0;

    while (size / toDivideBy > 1) {
        times += 1;
        size /= toDivideBy;
    }

    const roundedNumber = Math.round(size);
    const sizeEnding = getSizeEnding(times);
    return `${roundedNumber} ${sizeEnding}`;
};

const mockTh = 1;

const PackageUploadForm = () => {
    const [state, upload] = usePackageUpload({ th: mockTh });
    const [file, setFile] = useState(undefined);
    const fileInput = useRef(null);

    const handleSetFile = () => {
        const f = fileInput.current.files[0];
        setFile({ name: f.name, size: f.size });
    };

    const handleOnSubmitDone = () => {
        setFile(undefined);
    };

    const handleSubmit = (event) => {
        event.preventDefault();
        const f = fileInput.current.files[0];
        upload({ data: f, onDone: handleOnSubmitDone });
    };


    if (isLoading(state)) {
        return (
            <ProgressBar progress={state.progress} />
        );
    }

    return (
        <form onSubmit={handleSubmit} style={style.form}>
            <div style={style.innerLeftContainer}>
                <label style={style.fileLabel} htmlFor="file">
                    { file ? file.name : 'Browse files...'}
                    <input
                        type="file"
                        id="file"
                        name="file"
                        ref={fileInput}
                        onChange={handleSetFile}
                        style={style.fileInput}
                    />
                </label>

                {file && (
                    <div>
                        <p style={style.fileInfo}>
                            {file.name}
                            {' '}
                            {getFileSizeShortening(file.size)}
                        </p>
                    </div>
                )}
            </div>
            <div>
                <button
                    disabled={!file}
                    style={file ? defaultStyles.button : defaultStyles.buttonDisabled}
                    type="submit"
                >
                    Upload
                </button>
            </div>
        </form>
    );
};

export default PackageUploadForm;
